or: #fff1d4;\r\n  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);\r\n  border-radius: 0.5em;\r\n  outline: 0;\r\n}\r\n\r\n#emailClient::placeholder {\r\n  color: #2f435a8a;\r\n}\r\n\r\n.fone-client-container {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n  margin-top: 0.625em;\r\n  align-items: center;\r\n  width: 98%;\r\n  height: 1.875em;\r\n  gap: 0.15625em;\r\n}\r\n\r\n.fa-phone {\r\n  color: #2f435a;\r\n  font-size: 1.7em;\r\n  filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.2));\r\n}\r\n\r\n.fone-client-container input {\r\n  color: #2f435a;\r\n}\r\n\r\n#foneClient {\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n  line-height: 1.875em;\r\n  margin-left: 0.1875em;\r\n  padding-left: 0.5em;\r\n  width: 40.25em;\r\n  height: 1.875em;\r\n  color: rgb(47, 67, 90);\r\n  font-size: 1em;\r\n  border: none;\r\n  background-color: #fff1d4;\r\n  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);\r\n  border-radius: 0.5em;\r\n  outline: 0;\r\n}\r\n\r\n#foneClient::placeholder {\r\n  color: #2f435a8a;\r\n}\r\n\r\n/* FIM CLIENTE */\r\n\r\n\r\n/* INICIO PET */\r\n#pet {\r\n  display: flex;\r\n  flex-direction: column;\r\n  width: 100%;\r\n}\r\n\r\n.label-title-pet {\r\n  display: flex;\r\n  margin-right: auto;\r\n  margin-left: auto;\r\n  margin-top: 25px;\r\n  width: 100%;\r\n  height: 1.875em;\r\n  border-radius: 3.125em;\r\n  align-items: center;\r\n  background-color: #2f435a;\r\n  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.text-title-pet {\r\n  display: flex;\r\n  height: 1.25em;\r\n  padding-top: 0.0625em;\r\n  margin-left: 0.625em;\r\n  align-items: center;\r\n  color: #fdce6d;\r\n  font-size: 1.25em;\r\n}\r\n\r\n.name-pet-container {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n  margin-top: 0.625em;\r\n  align-items: center;\r\n  width: 98%;\r\n  height: 1.875em;\r\n  gap: 0.15625em;\r\n}\r\n\r\n.name