ex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  width: 100%;\r\n}\r\n\r\n.text-servicos {\r\n  display: flex;\r\n  margin-right: auto;\r\n  margin-left: auto;\r\n  margin-top: 1.25em;\r\n  padding-left: 10px;\r\n  width: 100%;\r\n  height: 30px;\r\n  border-radius: 3.125em;\r\n  align-items: center;\r\n  background-color: #2f435a;\r\n  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);\r\n  align-items: center;\r\n  color: #fdce6d;\r\n  font-size: 1.25em;\r\n}\r\n\r\n#services {\r\n  width: 98%;\r\n  height: 1.875em;\r\n  margin-top: 5px;\r\n  padding: 0 0.3125em;\r\n  align-items: center;\r\n  outline: 0;\r\n  border: none;\r\n  border-radius: 0.5em;\r\n  font-size: 1em;\r\n  line-height: 1.875em;\r\n  box-sizing: border-box;\r\n  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);\r\n  background-color: #fff1d4;\r\n  color: #2f435a;\r\n  cursor: pointer;\r\n}\r\n/* FIM SERVIÇOS */\r\n\r\n\r\n/* AGENDA */\r\n.label-title-agenda {\r\n  display: flex;\r\n  margin-right: auto;\r\n  margin-left: auto;\r\n  margin-top: 1.25em;\r\n  width: 100%;\r\n  height: 1.875em;\r\n  border-radius: 3.125em;\r\n  align-items: center;\r\n  background-color: #2f435a;\r\n  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.text-title-agenda {\r\n  display: flex;\r\n  height: 1.25em;\r\n  padding-top: 0.0625em;\r\n  margin-left: 0.625em;\r\n  align-items: center;\r\n  color: #fdce6d;\r\n  font-size: 1.25em;\r\n}\r\n\r\n#agenda {\r\n  justify-content: flex-start;\r\n  width: 40%;\r\n  margin-top: 0.625em;\r\n}\r\n\r\n#agenda input {\r\n  display: flex;\r\n  height: 1.875em;\r\n  margin-left: 0.4375em;\r\n  margin-top: 0.3125em;\r\n  padding-left: 0.625em;\r\n  padding-right: 0.3125em;\r\n  align-items: center;\r\n  background-color: #fff1d4;\r\n  border: none;\r\n  border-radius: 8px;\r\n  color: #2f435a;\r\n  font-size: 0.9375em;\r\n  outline: 0;\r\n  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);\r\n}\r\n/* FIM AGENDA */\r\n\r\n\r\n/* OBSERVAÇÕES */\r\n.label-title-obs {\r\n  di